const rules = { 
    required: (value) => !!value || 'Este campo precisa ser preenchido',
    counter: (value) => value.length <= 20 || 'At no mximo 20 caracteres',
    email: (value) => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'E-mail invlido'
    },
    specialCharacters: (value) => { 
        const pattern = /[^-\s\w]/g;
        return !pattern.test(value) || 'No  permitido caracteres especiais'
    },
    positiveNumber: (value) => {
        return value > 0 || 'No  permitido valor negativo'
    },
    positiveNotNull: (value) => {
        return value >= 0 || 'No  permitido valor negativo'
    }

};

export default rules;